const app = getApp();
const db = wx.cloud.database()
export default {
	data() {
		return {
			hotCityArr: [],
			cityArr: [],
			historyArr: [],
			location: '', // 需要查询的城市名称
			showSearch: false,
		}
	},
	onLoad() {
		this.getHotCity();
		this.getHistory();
	},
	methods: {
		addHistory(item) {
			let addObj = {
				uid: uni.getStorageSync('uid'),
				location: item.location,
				lat: item.lat,
				lng: item.lon
			}
			wx.cloud.callFunction({
				name: 'addHistory',
				data: {
					addObj
				}
			}).then(res => {
				console.log('addHistory res:', res)
			})
		},
		getHistory() {
			let that = this
			wx.cloud.callFunction({
				name: 'getHistory',
				data: {
					uid: uni.getStorageSync('uid')
				}
			}).then(res => {
				console.log('getHistory', res)
				that.historyArr = res.result.data
			})
		},
		clearHistory() {
			let that = this
			wx.cloud.callFunction({
				name: 'clearHistory',
				data: {
					uid: uni.getStorageSync('uid')
				}
			}).then(res => {
				console.log('clearHistory', res)
				if(res.result.errMsg == 'collection.remove:ok'){
					uni.showToast({
						title:'删除成功',
						icon:'none'
					})
					that.getHistory();
				}
			})
		},
		getHotCity() {
			let that = this
			let url = "https://search.heweather.net/top?group=world&key=" + app.globalData.key
			uni.request({
				url: url,
				success(res) {
					that.hotCityArr = res.data.HeWeather6[0].basic
					console.log('hot city:', that.hotCityArr)
				}
			})
		},
		getCity() {
			let that = this
			let url = 'https://search.heweather.net/find?location=' + this.location + '&key=' + app.globalData.key
			uni.request({
				url: url,
				success(res) {
					that.cityArr = res.data.HeWeather6[0].basic
					that.showSearch = true
					console.log('search city:', that.cityArr)
				}
			})
		},
		onConfirm(event) {
			this.location = event.target.value
			console.log('confirm location:', this.location)
			this.getCity()
		},
		chooseLocation(item) {
			let that = this
			let lngLat = item.lon + ',' + item.lat
			uni.setStorageSync('lngLat', lngLat)
			uni.setStorageSync('location', item.location)
			this.addHistory(item)
			uni.navigateBack({
				delta: 1
			})
		},
		chooseHis(item) {
			let that = this
			let lngLat = item.lng + ',' + item.lat
			uni.setStorageSync('lngLat', lngLat)
			uni.setStorageSync('location', item.location)
			uni.navigateBack({
				delta: 1
			})
		},
		goBack() {
			uni.navigateBack({
				delta: 1
			})
		}
	}
}
